@use 'index';

.tasks-container-grid {
  display: flex;
  flex-direction: column;
  gap: 0;
  overflow-wrap: break-word;
  width: 100%;
}

.task-entry{
  display: flex;
  align-items: center;
  grid-gap: 10px;
  border: medium solid index.$border-color;
  border-top: none;
  font-weight:  500;
  padding: 10px;
}

.task-entry p {
  flex-wrap: nowrap;
  margin-top: 0;
  margin-bottom: 0;
}

.task-entry:first-child{
  border-top: medium solid index.$border-color;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}

.task-entry:last-child{
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}

