@use 'index';

.add-task-form {
  display: flex;
  margin-top: 20px;
  align-items: center;
  width: 100%;
}

.add-task-input {
  box-sizing: border-box;
  margin-right: 20px;
  background-color: #fcfcfd;
  border: medium solid index.$border-color;
  border-radius: 6px;
  height: 40px;
  width: 100%;
  padding-left: 10px;
}

.add-task-submit {
  font-size: 20px;
  color: ghostwhite;
  background-color: #2d2929;
  border-color: index.$border-color;
  height: 40px;
  width: 40px;
  border-radius: 6px;
}