#root {
  display: flex;
  justify-content: center;
}

.app-root {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 320px) {
  .app-root {
    width: 70%;
  }
}

@media screen and (min-width: 1000px) {
  .app-root {
    width: 700px;
  }
}


@media screen and (max-width: 320px) {
  .app-root {
    width: 225px;
  }
}

.app-root>.title {
  color: #2E2E3A;
  font-size: 60px;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}


